import React from "react";

type inputProps = React.HTMLProps<HTMLInputElement>;
export const SimpleInput = (props: inputProps): JSX.Element => {
  return (
    <input
      className="drop-shadow-2xl rounded-xl py-4 px-2 outline-none w-[100%]"
      {...props}
    />
  );
};

type textAreaProps = React.HTMLProps<HTMLTextAreaElement>;
export const SimpleTextArea = React.forwardRef<
  HTMLTextAreaElement,
  textAreaProps
>((props, ref) => {
  return (
    <textarea
      ref={ref}
      className="drop-shadow-2xl rounded-xl py-4 px-2 outline-none w-[100%]"
      {...props}
    />
  );
});

type checkboxProps = React.HTMLProps<HTMLInputElement>;
export const SimpleCheckbox = ({ ...props }: checkboxProps) => {
  return (
    <input
      className="drop-shadow-2xl rounded-xl py-4 px-2 outline-none w-[100%]"
      type={"checkbox"}
      {...props}
    />
  );
};
