import Container from "../../../Components/Containers/Container";
import Section from "../../../Components/Containers/Section";
import "leaflet/dist/leaflet.css";
import L from "leaflet";
import { useEffect } from "react";

const ContactsSection: React.FC = (): JSX.Element => {
  useEffect(() => {
    const map = L.map("map", {
      scrollWheelZoom: false,
    }).setView([40.211389361687274, 28.98681478430292], 16);
    L.tileLayer("https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png").addTo(
      map
    );

    const icon = L.icon({
      iconUrl: require("../../../Assets/Images/mapPin.png"),
      iconSize: [50, 50],
      popupAnchor: [0, -20],
    });

    L.marker([40.211389361687274, 28.98681478430292], { icon })
      .addTo(map)
      .bindPopup("B Blok, 4. Kat <br /> No: 55 <br /> Konak, Nilüfer/Bursa")
      .openPopup();

    return () => {
      map.remove();
    };
  }, []);

  return (
    <Section>
      <Container className="flex flex-row justify-between items-center gap-4 max-w-[1100px]">
        <div
          className="w-[100%] h-[450px] rounded-xl overflow-hidden shadow-xl z-0"
          id="map"
        ></div>
      </Container>
    </Section>
  );
};

export default ContactsSection;
