import { useEffect, useRef, useState } from "react";

const useNotification = (duration = 3000) => {
  const [notificationTitle, setNotificationTitle] = useState<string>("");
  const [notificationType, setNotificationType] = useState<string>("");
  const [isOpen, setIsOpen] = useState<boolean>(false);
  const [notificationMessage, setNotificationMessage] = useState<string>("");
  const [notificationBg, setNotificationBg] = useState<string>("");
  const timerRef = useRef(null);
  const notificationRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (isOpen) {
      timerRef.current = setTimeout(() => {
        setIsOpen(false);
      }, duration);
    }
    return () => {
      clearTimeout(timerRef.current);
    };
  }, [isOpen]);

  const openNotification = (
    type?: string,
    title?: string,
    message?: string
  ): void => {
    setIsOpen(true);
    setNotificationType(type);
    setNotificationTitle(title);
    setNotificationMessage(message);
    switch (type) {
      case "success":
        setNotificationBg("bg-brand-2/60");
        break;
      case "error":
        setNotificationBg("bg-brand-2/60");
        break;
      default:
        setNotificationBg("bg-white/80");
        break;
    }
  };

  const NotificationComponent = (): JSX.Element => {
    return (
      <div
        className={`fixed top-1 right-1 z-50 flex justify-center items-center backdrop-blur-md ${
          isOpen ? "opacity-100 visible" : "opacity-0 invisible"
        } ${notificationBg} rounded-lg text-gray-900`}
        data-type={notificationType}
        ref={notificationRef}
        onMouseEnter={() => {
          clearTimeout(timerRef.current);
        }}
        onMouseLeave={() => {
          timerRef.current = setTimeout(() => {
            setIsOpen(false);
          }, duration);
        }}
      >
        <div className="w-fit h-fit p-4">
          <div className="w-full h-full flex flex-col justify-center items-center gap-4">
            <div className="flex flex-row justify-between items-center w-full">
              <p className="text-sm md:text-xl font-bold">
                {notificationTitle}
              </p>
              <button
                className="rounded-lg px-2 py-1 text-gray-900"
                onClick={() => setIsOpen(false)}
              >
                &#10006;
              </button>
            </div>
            <p className="text-base md:text-lg">{notificationMessage}</p>
          </div>
        </div>
      </div>
    );
  };

  return { openNotification, NotificationComponent };
};

export default useNotification;
