import { entity } from "simpler-state";
import { ContactTypes } from "./contact.types";

export const contactBloc = entity<ContactTypes>({
  name: "",
  phone: "",
  email: "",
  company: "",
  needs: {},
  message: "",
});

export const handleContactInputs = (
  event: React.ChangeEvent<HTMLInputElement>,
  field: keyof ContactTypes
) => {
  const newContact = contactBloc.get();
  field === "needs"
    ? (newContact[field] = {
        ...newContact[field],
        [event.target.name]: event.target.checked,
      })
    : (newContact[field] = event.target.value);
  contactBloc.set({ ...newContact });
};

export const handleContactTextArea = (
  event: React.ChangeEvent<HTMLTextAreaElement>
) => {
  const newContact = contactBloc.get();
  newContact.message = event.target.value;
  contactBloc.set({ ...newContact });
};

export const clearContact = () => {
  contactBloc.set({
    name: "",
    phone: "",
    email: "",
    company: "",
    needs: {},
    message: "",
  });
};

export const hurryContent = entity<string[]>([]);

export const handleHurryContent = (
  event: React.ChangeEvent<HTMLInputElement>
): void => {
  const { value, checked } = event.target;
  if (checked) {
    hurryContent.set([...hurryContent.get(), value]);
  } else {
    hurryContent.set(hurryContent.get().filter((item) => item !== value));
  }
};

export const clearHurryContent = (): void => {
  hurryContent.set([]);
};
