import { ReactComponent as InstagramSVG } from "../../../Assets/SVGs/newsletter/instagram.svg";
import { ReactComponent as TwitterSVG } from "../../../Assets/SVGs/newsletter/twitter.svg";
import { ReactComponent as LinkedinSVG } from "../../../Assets/SVGs/newsletter/linkedin.svg";
import { ReactComponent as NewsletterLineSVG } from "../../../Assets/SVGs/newsletter/newsletterLine.svg";
import { ReactComponent as MailSVG } from "../../../Assets/SVGs/newsletter/mail.svg";

interface Props {
  className?: string;
}

export const NewsletterInstagram = (): JSX.Element => {
  const onClick = (): void => {
    window.open("https://www.instagram.com/zalisoft", "_blank");
  };

  return <InstagramSVG onClick={onClick} className="cursor-pointer" />;
};

export const NewsletterTwitter = (): JSX.Element => {
  const onClick = (): void => {
    window.open("https://www.twitter.com/zalisoft", "_blank");
  };

  return <TwitterSVG onClick={onClick} className="cursor-pointer" />;
};

export const NewsletterLinkedin = (): JSX.Element => {
  const onClick = (): void => {
    window.open("https://www.linkedin.com/company/zalisoft", "_blank");
  };

  return <LinkedinSVG onClick={onClick} className="cursor-pointer" />;
};

export const NewsletterLine = (): JSX.Element => {
  return <NewsletterLineSVG />;
};

export const NewsletterMail = (props: Props): JSX.Element => {
  return <MailSVG className={props.className} />;
};
