import { useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import { homePage } from "../../BLoC/Toggles";
import { LogoMonoText, LogoText } from "../Common/SVGs/Logos";
import { MenuIcon, MenuIconMono } from "../Common/SVGs/MenuIcon";
import NavbarSVG from "../Common/SVGs/NavbarBg";
import Container from "../Containers/Container";

const Navbar = () => {
  const isHomePage = homePage.use();
  const [scrollYPosition, setScrollYPosition] = useState<number>(0);
  const navigate = useNavigate();
  const [isMenuOpen, setIsMenuOpen] = useState<boolean>(false);

  const onWindowScroll = () => {
    setScrollYPosition(window.scrollY);
  };

  useEffect(() => {
    window.addEventListener("scroll", onWindowScroll);

    return () => {
      window.removeEventListener("scroll", onWindowScroll);
    };
  }, []);

  useEffect(() => {
    if (isMenuOpen) document.body.style.overflow = "hidden";
    else document.body.style.overflow = "";
  }, [isMenuOpen]);

  return (
    <nav
      className={`flex flex-row w-full fixed top-0 z-20 navbar-start-anim ${
        (isHomePage && scrollYPosition === 0) || isMenuOpen
          ? "h-[160px]"
          : "bg-white shadow-lg h-[86px]"
      }`}
    >
      <Container className="z-20">
        <div className="flex w-full m-auto justify-between items-center">
          {isHomePage && scrollYPosition === 0 && !isMenuOpen ? (
            <div
              className="cursor-pointer w-full max-w-[200px] lg:max-w-[300px] xl:max-w-[400px]"
              onClick={() => navigate("/")}
            >
              <LogoMonoText className="w-full h-auto" />
            </div>
          ) : (
            <div
              className="cursor-pointer w-full max-w-[200px] lg:max-w-[260px] xl:max-w-[300px]"
              onClick={() => {
                setIsMenuOpen(false);
                navigate("/");
              }}
            >
              <LogoText className="w-full h-auto" />
            </div>
          )}

          <div
            className={`hidden flex-row items-center gap-16 ml-auto duration-75 lg:flex ${
              isHomePage && scrollYPosition === 0 && !isMenuOpen
                ? "text-white"
                : "text-brand-1"
            }`}
          >
            <button
              onClick={() => setIsMenuOpen(!isMenuOpen)}
              className={`text-xl px-5 py-2 border-2 border-[transparent] rounded-md hover:bg-brand-1 hover:text-white`}
            >
              Menü
            </button>
            <button
              onClick={(): void => {
                setIsMenuOpen(false);
                navigate("/contact");
              }}
              className={`text-xl rounded-md px-5 py-2 border-2  ${
                isHomePage && scrollYPosition === 0 && !isMenuOpen
                  ? "border-white hover:bg-white hover:text-brand-1"
                  : "border-brand-1 hover:bg-brand-1 hover:text-white"
              }`}
            >
              İletişim
            </button>
          </div>

          <div className="block lg:hidden">
            {isHomePage && scrollYPosition === 0 && !isMenuOpen ? (
              <div
                onClick={() => {
                  setIsMenuOpen(!isMenuOpen);
                }}
              >
                <MenuIconMono />
              </div>
            ) : (
              <div
                onClick={() => {
                  setIsMenuOpen(!isMenuOpen);
                }}
              >
                <MenuIcon />
              </div>
            )}
          </div>
        </div>
      </Container>
      <div
        className={`fixed w-full h-fit top-0 z-10 rounded-b-2xl shadow-2xl bg-white navbar-menu-in ${
          isMenuOpen ? "opacity-100" : "opacity-0 translate-y-[-100%]"
        }`}
      >
        <Container className="relative py-40">
          <NavbarSVG className="absolute top-0 -right-20 h-[160px] w-auto" />

          <div className="flex flex-row justify-center items-center gap-6">
            <Link
              className="text-brand-1 lg:text-xl cursor-pointer underline"
              to="/services"
              onClick={() => {
                setIsMenuOpen(false);
              }}
            >
              Hizmetlerimiz
            </Link>
            <Link
              className="text-brand-1 lg:text-xl cursor-pointer underline"
              to="/contact"
              onClick={() => {
                setIsMenuOpen(false);
              }}
            >
              İletişim
            </Link>
          </div>
        </Container>
      </div>
    </nav>
  );
};

export default Navbar;
