import React, { useEffect, useRef, useState } from "react";

type ICarouselProps = React.HTMLAttributes<HTMLDivElement>;
const Carousel = ({ children, ...props }: ICarouselProps): JSX.Element => {
  const carouselRef = useRef<HTMLDivElement>(null);
  const intervalRef = useRef(null);
  const duration = 5000;
  const childrenLength = React.Children.count(children);
  const [currentIndex, setCurrentIndex] = useState<number>(1);

  const intervalFunction = (): void => {
    carouselRef.current.scrollTo({
      top: 0,
      left: carouselRef.current.scrollLeft + carouselRef.current.clientWidth,
      behavior: "smooth",
    });
    setCurrentIndex((val) => val + 1);

    if (
      carouselRef.current.scrollLeft ===
      carouselRef.current.scrollWidth - carouselRef.current.clientWidth
    ) {
      carouselRef.current.scrollTo({
        top: 0,
        left: 0,
        behavior: "smooth",
      });
      setCurrentIndex(1);
    }
  };

  useEffect(() => {
    intervalRef.current = setInterval(intervalFunction, duration);

    return () => {
      clearInterval(intervalRef.current);
    };
  }, []);

  return (
    <div>
      <div
        ref={carouselRef}
        {...props}
        onMouseEnter={() => clearInterval(intervalRef.current)}
        onMouseLeave={() =>
          (intervalRef.current = setInterval(intervalFunction, duration))
        }
        className="flex flex-row justify-start items-start overflow-x-hidden w-full hover:bg-customBlack/5"
      >
        {children}
      </div>
      <ul className="flex flex-row justify-center gap-2">
        {Array.from(Array(childrenLength).keys()).map((index) => (
          <li
            key={index}
            className={`${
              currentIndex === index + 1 ? "bg-brand-1" : "bg-brand-2"
            } w-2 h-2 rounded-full flex flex-row items-center justify-center mt-4`}
          ></li>
        ))}
      </ul>
    </div>
  );
};
export default Carousel;

export const CarouselItem = ({
  children,
  ...props
}: ICarouselProps): JSX.Element => {
  return (
    <div {...props} className="w-full shrink-0">
      {children}
    </div>
  );
};

type ICarouselItemImageProps = React.ImgHTMLAttributes<HTMLImageElement>;
export const CarouselItemImage = (
  props: ICarouselItemImageProps
): JSX.Element => {
  return <img {...props} className="w-full h-full object-cover" />;
};
