import { useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { setNavInvis } from "../BLoC/Toggles";
import {
  DarkBlueBorderedButton,
  DarkBlueButton,
} from "../Components/Common/Buttons";
import Container from "../Components/Containers/Container";

const PageNotFound = (): JSX.Element => {
  const navigate = useNavigate();

  useEffect(() => {
    setNavInvis(true);

    return () => {
      setNavInvis(false);
    };
  }, []);

  const navigation = (nav: string): void => {
    navigate(nav);
  };

  return (
    <section className="mt-40 mb-40">
      <Container className="flex flex-col items-center">
        <div className="text-[8rem] lg:text-[12.5rem] leading-tight font-black text-brand-1 flex flex-row">
          <h1>40</h1>
          <h1 className="rotate-12">4</h1>
        </div>
        <p className="text-base lg:text-2xl font-light">
          Burada içerik yok. Lütfen ana sayfaya gidin.
        </p>
        <div className="flex flex-row gap-4 mt-10 w-[100%] max-w-[380px] lg:max-w-[468px]">
          <DarkBlueBorderedButton
            onClick={() => navigation("/")}
            className="basis-1/2"
          >
            Anasayfa
          </DarkBlueBorderedButton>
          <DarkBlueButton
            onClick={() => navigation("/contact")}
            className="basis-1/2"
          >
            İletişim
          </DarkBlueButton>
        </div>
      </Container>
    </section>
  );
};

export default PageNotFound;
