import { keyframes } from "@emotion/react";
import Reveal from "react-awesome-reveal";

type IRevProps = {
  children: React.ReactNode;
  delay?: number;
  duration?: number;
  className?: string;
};

export const RevealFromBottom = (props: IRevProps) => {
  const revealFromBottom = keyframes`
    from {
      opacity: 0;
      transform: translateY(10px);
      }
      to {
        opacity: 1;
        transform: translateY(0);
      }
    `;

  return (
    <Reveal
      keyframes={revealFromBottom}
      delay={props.delay ? props.delay : 200}
      duration={1800}
      className={`w-full ${props.className ? props.className : ""}`}
      triggerOnce={true}
    >
      {props.children}
    </Reveal>
  );
};

