import { ReactComponent as FooterLogoSVG } from "../../../Assets/SVGs/footer/footerLogo.svg";
import { ReactComponent as InstagramLogoSVG } from "../../../Assets/SVGs/footer/instagram.svg";
import { ReactComponent as TwitterLogoSVG } from "../../../Assets/SVGs/footer/twitter.svg";
import { ReactComponent as LinkedinLogoSVG } from "../../../Assets/SVGs/footer/linkedin.svg";

export const FooterLogo = (): JSX.Element => {
  return (
    <FooterLogoSVG className="basis-1/3 w-full max-w-[240px] lg:max-w-max lg:w-auto" />
  );
};

export const InstagramLogo = (): JSX.Element => {
  const onClick = (): void => {
    window.open("https://www.instagram.com/zalisoft", "_blank");
  };

  return <InstagramLogoSVG onClick={onClick} className="cursor-pointer" />;
};

export const TwitterLogo = (): JSX.Element => {
  const onClick = (): void => {
    window.open("https://www.twitter.com/zalisoft", "_blank");
  };

  return <TwitterLogoSVG onClick={onClick} className="cursor-pointer" />;
};

export const LinkedinLogo = (): JSX.Element => {
  const onClick = (): void => {
    window.open("https://www.linkedin.com/company/zalisoft", "_blank");
  };

  return <LinkedinLogoSVG onClick={onClick} className="cursor-pointer" />;
};
