import {
  clearContact,
  clearHurryContent,
  contactBloc,
  handleContactInputs,
  handleContactTextArea,
  hurryContent,
} from "../../../BLoC/contact.bloc/contact.bloc";
import { RevealFromBottom } from "../../../Components/Animations/RevealComponents";
import { SubmitButton } from "../../../Components/Contact/Buttons";
import {
  CheckboxContainer,
  FormCol,
  FormContainer,
  FormLabel,
  FormRow,
  TextAreaContainer,
} from "../../../Components/Contact/FormElements";
import {
  SimpleInput,
  SimpleTextArea,
} from "../../../Components/Contact/Inputs";
import Container from "../../../Components/Containers/Container";
import { SectionTitle } from "../../../Components/Home/Titles";
import { send } from "emailjs-com";
import useNotification from "../../../Components/Hooks/useNotification";
import { useEffect, useRef } from "react";

const FormSection: React.FC = (): JSX.Element => {
  const contactForm = contactBloc.use();
  const { openNotification, NotificationComponent } = useNotification();
  const textareaRef = useRef<HTMLTextAreaElement>(null);
  const hurryInstance = hurryContent.use();

  useEffect(() => {
    if (hurryInstance.length > 0) {
      const hurryString = hurryInstance.join(", ");
      textareaRef.current.innerText = hurryString;
    }

    return () => {
      clearHurryContent();
    };
  }, []);

  const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    const templateParams = {
      name: contactForm.name,
      phone: contactForm.phone,
      email: contactForm.email,
      company: contactForm.company,
      needs: JSON.stringify(contactForm.needs),
      message: contactForm.message,
    };
    send(
      "service_7k66hrc",
      "template_2fsv2v9",
      templateParams,
      "user_CdNvUys1YCdD3KpCpi6Bf"
    ).then(
      () => {
        openNotification(
          "success",
          "Başarılı",
          "E-postanız başarıyla gönderildi."
        );
      },
      () => {
        openNotification("error", "Hata", "E-posta gönderilemedi.");
      }
    );
    event.currentTarget.reset();
    clearContact();
  };

  return (
    <section className="pt-[200px]">
      <NotificationComponent />
      <RevealFromBottom>
        <Container>
          <SectionTitle title="Bize Ulaşın" />
          <FormContainer onSubmit={handleSubmit}>
            <FormRow>
              <FormCol>
                <FormLabel htmlFor="name">İsminiz</FormLabel>
                <SimpleInput
                  type={"text"}
                  name="name"
                  id="name"
                  placeholder="İsminiz"
                  required
                  value={contactForm.name}
                  onChange={(event: React.ChangeEvent<HTMLInputElement>) =>
                    handleContactInputs(event, "name")
                  }
                />
              </FormCol>
              <FormCol>
                <FormLabel htmlFor="phone">Telefon</FormLabel>
                <SimpleInput
                  type={"tel"}
                  name="phone"
                  id="phone"
                  placeholder="Telefon numaranız"
                  required
                  value={contactForm.phone}
                  onChange={(event: React.ChangeEvent<HTMLInputElement>) =>
                    handleContactInputs(event, "phone")
                  }
                />
              </FormCol>
            </FormRow>
            <FormRow>
              <FormCol>
                <FormLabel htmlFor="email">E-posta</FormLabel>
                <SimpleInput
                  type={"email"}
                  name="email"
                  id="email"
                  placeholder="E-posta"
                  required
                  value={contactForm.email}
                  onChange={(event: React.ChangeEvent<HTMLInputElement>) =>
                    handleContactInputs(event, "email")
                  }
                />
              </FormCol>
              <FormCol>
                <FormLabel htmlFor="company">Şirketiniz</FormLabel>
                <SimpleInput
                  type={"text"}
                  name="company"
                  id="company"
                  placeholder="Şirketiniz"
                  value={contactForm.company}
                  onChange={(event: React.ChangeEvent<HTMLInputElement>) =>
                    handleContactInputs(event, "company")
                  }
                />
              </FormCol>
            </FormRow>

            <div className="w-full my-10">
              <FormLabel>Neye İhtiyacınız var?</FormLabel>
              <div className="flex flex-row justify-start items-start w-full gap-8 max-w-2xl mt-8 flex-wrap self-start">
                <CheckboxContainer
                  label="Kurumsal Websitesi"
                  name="kurumsalWeb"
                  id="kurumsalWeb"
                  checkboxProps={{
                    onChange: (event: React.ChangeEvent<HTMLInputElement>) =>
                      handleContactInputs(event, "needs"),
                  }}
                />
                <CheckboxContainer
                  label="E-Ticaret Websitesi"
                  name="ecommerceWeb"
                  id="ecommerceWeb"
                  checkboxProps={{
                    onChange: (event: React.ChangeEvent<HTMLInputElement>) =>
                      handleContactInputs(event, "needs"),
                  }}
                />
                <CheckboxContainer
                  label="Backend"
                  name="backend"
                  id="backend"
                  checkboxProps={{
                    onChange: (event: React.ChangeEvent<HTMLInputElement>) =>
                      handleContactInputs(event, "needs"),
                  }}
                />
                <CheckboxContainer
                  label="Frontend"
                  name="frontend"
                  id="frontend"
                  checkboxProps={{
                    onChange: (event: React.ChangeEvent<HTMLInputElement>) =>
                      handleContactInputs(event, "needs"),
                  }}
                />
                <CheckboxContainer
                  label="Devops"
                  name="devops"
                  id="devops"
                  checkboxProps={{
                    onChange: (event: React.ChangeEvent<HTMLInputElement>) =>
                      handleContactInputs(event, "needs"),
                  }}
                />
                <CheckboxContainer
                  label="UI/UX Dizayn"
                  name="uiux"
                  id="uiux"
                  checkboxProps={{
                    onChange: (event: React.ChangeEvent<HTMLInputElement>) =>
                      handleContactInputs(event, "needs"),
                  }}
                />
                <CheckboxContainer
                  label="Danışmanlık"
                  name="consultancy"
                  id="consultancy"
                  checkboxProps={{
                    onChange: (event: React.ChangeEvent<HTMLInputElement>) => {
                      handleContactInputs(event, "needs");
                    },
                  }}
                />
              </div>
            </div>

            <TextAreaContainer>
              <FormLabel htmlFor="request">Talebiniz</FormLabel>
              <SimpleTextArea
                id="request"
                name="request"
                placeholder="Talebinizi daha detaylı anlatır mısınız?"
                rows={5}
                required
                value={contactForm.message}
                onChange={(event: React.ChangeEvent<HTMLTextAreaElement>) =>
                  handleContactTextArea(event)
                }
                ref={textareaRef}
              />
            </TextAreaContainer>
            <SubmitButton>Gönder</SubmitButton>
          </FormContainer>
        </Container>
      </RevealFromBottom>
    </section>
  );
};

export default FormSection;
