import { useState } from "react";
import { HurryBorderedButton, HurryButton } from "../Common/Buttons";
import {
  clearHurryContent,
  handleHurryContent,
  hurryContent,
} from "../../BLoC/contact.bloc/contact.bloc";
import { useNavigate } from "react-router-dom";

const HurryForm = (): JSX.Element => {
  const [tabs, setTabs] = useState<"web" | "ui" | "devops">("web");
  const navigate = useNavigate();
  const hurryInstance = hurryContent.use();

  const tabChange = (tab: "web" | "ui" | "devops"): void => {
    clearHurryContent();
    setTabs(tab);
  };

  const handleSubmit = (event: React.FormEvent<HTMLFormElement>): void => {
    event.preventDefault();
    event.currentTarget.reset();
    if (hurryInstance.length !== 0) navigate("/contact");
  };

  return (
    <div className="mt-20">
      <div className="flex flex-row justify-center items-center gap-8 md:gap-16">
        <TabTitle onClick={() => tabChange("web")} isActive={tabs === "web"}>
          Web
        </TabTitle>
        <TabTitle onClick={() => tabChange("ui")} isActive={tabs === "ui"}>
          UI/UX
        </TabTitle>
        <TabTitle
          onClick={() => tabChange("devops")}
          isActive={tabs === "devops"}
        >
          DevOps
        </TabTitle>
      </div>
      <div className="mt-4 bg-white shadow-2xl w-full rounded-xl py-10 px-10">
        <p className="text-center text-base md:text-xl font-medium">
          Bizimle iletişime geçmek için bir hizmet seçiniz.
        </p>
        <div className="flex flex-col justify-center items-center gap-16">
          <form className="flex flex-col gap-16" onSubmit={handleSubmit}>
            <div className="flex flex-col justify-center items-center gap-16 mt-10">
              {tabs === "web" && (
                <div className="flex flex-row justify-center flex-wrap gap-8">
                  <HurryCheckboxContainer
                    content="Kurumsal Web Sitesi"
                    inputProps={{
                      id: "kurumsalWeb",
                      name: "hurry",
                      type: "checkbox",
                      value: "kurumsalWeb",
                      onChange: (e: React.ChangeEvent<HTMLInputElement>) =>
                        handleHurryContent(e),
                    }}
                    labelProps={{
                      htmlFor: "kurumsalWeb",
                    }}
                  />
                  <HurryCheckboxContainer
                    content="E-Ticaret Web Sitesi"
                    inputProps={{
                      id: "ecommerceWeb",
                      name: "hurry",
                      type: "checkbox",
                      value: "ecommerceWeb",
                      onChange: (e: React.ChangeEvent<HTMLInputElement>) =>
                        handleHurryContent(e),
                    }}
                    labelProps={{
                      htmlFor: "ecommerceWeb",
                    }}
                  />
                  <HurryCheckboxContainer
                    content="Backend"
                    inputProps={{
                      id: "backend",
                      name: "hurry",
                      type: "checkbox",
                      value: "backend",
                      onChange: (e: React.ChangeEvent<HTMLInputElement>) =>
                        handleHurryContent(e),
                    }}
                    labelProps={{
                      htmlFor: "backend",
                    }}
                  />
                  <HurryCheckboxContainer
                    content="Frotend"
                    inputProps={{
                      id: "frontend",
                      name: "hurry",
                      type: "checkbox",
                      value: "frontend",
                      onChange: (e: React.ChangeEvent<HTMLInputElement>) =>
                        handleHurryContent(e),
                    }}
                    labelProps={{
                      htmlFor: "frontend",
                    }}
                  />
                </div>
              )}
              {tabs === "ui" && (
                <div className="flex flex-row justify-center flex-wrap gap-8">
                  <HurryCheckboxContainer
                    content="Web Tasarımı"
                    inputProps={{
                      id: "webDesign",
                      name: "hurry",
                      type: "checkbox",
                      value: "webDesign",
                      onChange: (e: React.ChangeEvent<HTMLInputElement>) =>
                        handleHurryContent(e),
                    }}
                    labelProps={{
                      htmlFor: "webDesign",
                    }}
                  />
                  <HurryCheckboxContainer
                    content="Ürün Tasarımı"
                    inputProps={{
                      id: "productDesign",
                      name: "hurry",
                      type: "checkbox",
                      value: "productDesign",
                      onChange: (e: React.ChangeEvent<HTMLInputElement>) =>
                        handleHurryContent(e),
                    }}
                    labelProps={{
                      htmlFor: "productDesign",
                    }}
                  />
                  <HurryCheckboxContainer
                    content="Logo Tasarımı"
                    inputProps={{
                      id: "logoDesign",
                      name: "hurry",
                      type: "checkbox",
                      value: "logoDesign",
                      onChange: (e: React.ChangeEvent<HTMLInputElement>) =>
                        handleHurryContent(e),
                    }}
                    labelProps={{
                      htmlFor: "logoDesign",
                    }}
                  />
                </div>
              )}
              {tabs === "devops" && (
                <div className="flex flex-row justify-center flex-wrap gap-8">
                  <HurryCheckboxContainer
                    content="DevOps"
                    inputProps={{
                      id: "devOps",
                      name: "hurry",
                      type: "checkbox",
                      value: "devOps",
                      onChange: (e: React.ChangeEvent<HTMLInputElement>) =>
                        handleHurryContent(e),
                    }}
                    labelProps={{
                      htmlFor: "devOps",
                    }}
                  />
                  <HurryCheckboxContainer
                    content="CI/CD"
                    inputProps={{
                      id: "cicd",
                      name: "hurry",
                      type: "checkbox",
                      value: "cicd",
                      onChange: (e: React.ChangeEvent<HTMLInputElement>) =>
                        handleHurryContent(e),
                    }}
                    labelProps={{
                      htmlFor: "cicd",
                    }}
                  />
                  <HurryCheckboxContainer
                    content="GCloud"
                    inputProps={{
                      id: "gcloud",
                      name: "hurry",
                      type: "checkbox",
                      value: "gcloud",
                      onChange: (e: React.ChangeEvent<HTMLInputElement>) =>
                        handleHurryContent(e),
                    }}
                    labelProps={{
                      htmlFor: "gcloud",
                    }}
                  />
                </div>
              )}
            </div>

            <div className="flex flex-row justify-center items-center gap-6">
              <HurryBorderedButton>Sıfırla</HurryBorderedButton>
              <HurryButton>Uygula</HurryButton>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default HurryForm;

type ITabTitleProps = {
  isActive: boolean;
} & React.HTMLAttributes<HTMLButtonElement>;
const TabTitle = ({
  isActive,
  className,
  children,
  ...props
}: ITabTitleProps): JSX.Element => {
  return (
    <button
      className={`basis-1/4 md:basis-1/5 shadow-[0_-4px_32px_rgba(0,0,0,0.10)] rounded-xl py-2 text-base md:text-2xl font-medium relative ${
        className === undefined ? "" : className
      } ${
        isActive
          ? "after:content-[' '] after:absolute after:bg-white after:-bottom-8 after:w-full after:h-10 after:left-0"
          : ""
      }`}
      {...props}
    >
      {children}
    </button>
  );
};

type IHurryCheckboxContainerProps = {
  content: string;
  inputProps: React.HTMLProps<HTMLInputElement>;
  labelProps: React.HTMLProps<HTMLLabelElement>;
} & React.HTMLAttributes<HTMLDivElement>;
const HurryCheckboxContainer = ({
  content,
  inputProps,
  labelProps,
}: IHurryCheckboxContainerProps): JSX.Element => {
  return (
    <div className="flex flex-row justify-center items-center gap-2">
      <input
        className="appearance-none rounded-md bg-white p-3 cursor-pointer shadow-md border-[1px] border-inputGray checked:bg-brand-1"
        {...inputProps}
      />
      <label
        className="inline-block cursor-pointer hover:underline text-base md:text-xl"
        {...labelProps}
      >
        {content}
      </label>
    </div>
  );
};
