import React from "react";
import {
  clearNewsletter,
  handleNewsletterChange,
  newsletterBloc,
} from "../../BLoC/newsletter.bloc/newsletter.bloc";
import Container from "../Containers/Container";
import Section from "../Containers/Section";
import {
  NewsletterInstagram,
  NewsletterLine,
  NewsletterLinkedin,
  NewsletterMail,
  NewsletterTwitter,
} from "./SVGs/NewsletterLogos";
import { send } from "emailjs-com";
import useNotification from "../Hooks/useNotification";

const Newsletter: React.FC = (): JSX.Element => {
  const newsletterInstance = newsletterBloc.use();
  const { openNotification, NotificationComponent } = useNotification();

  const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    const templateParams = {
      email: newsletterInstance.email,
    };
    send(
      "service_7k66hrc",
      "template_qr64nru",
      templateParams,
      "user_CdNvUys1YCdD3KpCpi6Bf"
    ).then(
      () => {
        openNotification(
          "success",
          "Başarılı",
          "E-postanız başarıyla gönderildi."
        );
      },
      () => {
        openNotification("error", "Hata", "E-posta gönderilemedi.");
      }
    );
    event.currentTarget.reset();
    clearNewsletter();
  };

  return (
    <Section>
      <NotificationComponent />
      <Container className="max-w-[987px]">
        <div className="flex flex-row items-center justify-between gap-2">
          <p className="text-2xl text-brand-1">Zalisoft</p>
          <NewsletterLine />
          <div className="flex flex-row gap-2">
            <NewsletterInstagram />
            <NewsletterTwitter />
            <NewsletterLinkedin />
          </div>
        </div>
        <div className="flex flex-col lg:flex-row justify-between items-center lg:items-start gap-4 mt-10 lg:mt-20 mb-60">
          <div className="max-w-[430px] basis-1/2">
            <h2 className="text-2xl lg:text-4xl text-brand-1 font-bold my-4">
              Güncellemelerden haberdar olun
            </h2>
            <p className="text-base lg:text-lg">
              Mail listemize kayıt olarak güncellemelerden ilk siz haberdar
              olabilirsiniz.
            </p>
          </div>
          <form
            className="flex flex-col basis-1/2 max-w-[490px] p-6 rounded-2xl shadow-2xl"
            onSubmit={handleSubmit}
          >
            <label htmlFor="email">E-Posta</label>
            <div className="flex flex-row bg-inputGray rounded py-3 px-4">
              <input
                className="appearance-none block bg-inputGray leading-tight focus:outline-none w-[100%]"
                type={"email"}
                placeholder="ornek@ornek.com"
                id="email"
                name="email"
                value={newsletterInstance.email}
                onChange={(event: React.ChangeEvent<HTMLInputElement>) =>
                  handleNewsletterChange(event)
                }
                required
              />
              <NewsletterMail />
            </div>
            <button className="w-[100%] bg-brand-1 rounded py-3 mt-6 mb-4 text-white">
              Katıl
            </button>
          </form>
        </div>
      </Container>
    </Section>
  );
};

export default Newsletter;
