import { SimpleCheckbox } from "./Inputs";

interface Props {
  children: React.ReactNode;
}

type LabelProps = React.HTMLProps<HTMLLabelElement>;
type FormContainerProps = React.HTMLProps<HTMLFormElement>;

export const FormContainer = ({
  children,
  ...props
}: FormContainerProps): JSX.Element => {
  return (
    <form
      className="flex flex-col justify-center items-center mt-20 max-w-[960px] mx-auto"
      {...props}
    >
      {children}
    </form>
  );
};

export const FormRow = (props: Props): JSX.Element => {
  return (
    <div className="flex flex-col md:flex-row justify-between basis-1/2 gap-4 md:my-4 w-[100%]">
      {props.children}
    </div>
  );
};

export const FormCol = (props: Props): JSX.Element => {
  return (
    <div className="flex flex-col w-[100%] max-w-[400px] my-4 md:my-0">
      {props.children}
    </div>
  );
};

export const FormLabel = (props: LabelProps) => {
  return <label {...props}>{props.children}</label>;
};

export const TextAreaContainer = (props: Props) => {
  return <div className="flex flex-col w-[100%] my-8">{props.children}</div>;
};

interface CheckboxProps {
  name: string;
  id: string;
  label: string;
  checkboxProps?: React.HTMLAttributes<HTMLInputElement>;
}
export const CheckboxContainer = (props: CheckboxProps) => {
  return (
    <div className="flex flex-row justify-center items-center gap-2">
      <SimpleCheckbox
        name={props.name}
        id={props.id}
        {...props.checkboxProps}
        className="appearance-none rounded-md bg-white p-4 cursor-pointer shadow-lg border-[1px] border-inputGray checked:bg-brand-1"
      />
      <FormLabel htmlFor={props.id}>{props.label}</FormLabel>
    </div>
  );
};
