import { entity } from "simpler-state";
import { Newsletter } from "./newsletter.types";

export const newsletterBloc = entity<Newsletter>({
  email: "",
});

export const handleNewsletterChange = (
  event: React.ChangeEvent<HTMLInputElement>
) => {
  newsletterBloc.set({
    email: event.target.value,
  });
};

export const clearNewsletter = () => {
  newsletterBloc.set({
    email: "",
  });
};
